.intro {
  display: flex;
  height: 77vh;
  margin-top: 5rem;
}

.i-left {
  display: flex;
  position: relative;
  flex: 1;
  flex-direction: column;
  gap: 2rem;
}
.i-name {
  display: flex;
  flex-direction: column;
}
.i-name > :nth-child(1) {
  color: var(--black);
  font-weight: bold;
  font-size: 3rem;
}

.i-name > :nth-child(2) {
  color: var(--orange);
  font-weight: bold;
  font-size: 3rem;
}

.i-name > :nth-child(3) {
  color: var(--gray);
  font-weight: 100;
  font-size: 14px;
  margin-top: 20px;
}

.i-button {
  width: 7rem;
  height: 2rem;
}

.i-icons {
  margin-top: 0.5rem;
  display: flex;
  gap: 0rem;
}
.i-icons > * {
  transform: scale(0.3);
}

.i-icons > :hover {
  cursor: pointer;
}

.i-right {
  flex: 1;
  position: relative;
}
.i-right > * {
  position: absolute;
  z-index: 1;
}
.i-right > :nth-child(1) {
  transform: scale(0.69);
  left: -15%;
  top: -9%;
}
.i-right > :nth-child(2) {
  transform: scale(0.67);
  left: -3rem;
  top: -4.6rem;
}
.i-right > :nth-child(3) {
  transform: scale(0.7);
  left: 3.5%;
  top: -20%;
}

.i-right > :nth-child(4) {
  transform: scale(0.5);
  left: -24%;
  top: -18%;
  border-radius: 50%;
  padding: 0;
}
.blur {
  position: absolute;
  width: 22rem;
  height: 14rem;
  border-radius: 50%;
  background: #edd0ff;
  z-index: -9;
  top: -18%;
  right: -15%;
  filter: blur(72px);
}
